import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue'
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import wPicker from "@/components/w-picker/w-picker.vue";
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	components: {
		uniSwipeAction,
		uniSwipeActionItem,
		uniLoadMore,
		empty,
		wPicker
	},
	data() {
		return {
			defaultBank:['中国工商银行'],
			bankList:this.$api.util.bankList,
			bankAccountName:'',
			bankName:'',
			bankAccountNo:'',
			alipayId:'',
			withdrawAmount: 0.00,
			withdrawList: [],
			taxRate:0,
			pageNo: 1,
			pageSize: 20,
			loadingType:'',
			navList: [{
					state: '1',
					text: '银行卡'
				},
				{
					state: '2',
					text: '支付宝'
				}],
			tabCurrentIndex:0,
		}
	},
	onLoad(option) {
		this.inquiryTaxRate();
		this.tabClick(0);
	},
	onReachBottom() {
		
	},
	//下拉刷新
	onPullDownRefresh() {
		
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		...mapMutations(['login']),
		inquiryTaxRate() {
			this.$api.request.getParameter({
				name: 'TAX_RATE'
			}, res => {
				if (res.body.status.statusCode === '0') {
					if(res.body.data){
						this.taxRate = res.body.data.value;
					}
				} else {
					console.log(res.body.status.errorDesc);
				}
			});
		},
		applyWithdraw(){
			//检查金额
			if(!this.withdrawAmount){
				this.$api.msg('请输入提现金额');
				return;
			}
			if(this.withdrawAmount===0 || this.withdrawAmount>this.userInfo.availableBalance){
				this.$api.msg('仅限金额输入有误');
				return;
			}
			//如果是银行卡提现, 检查各项输入是否正确
			if(this.tabCurrentIndex==0){
				if(!this.bankAccountName){
					this.$api.msg('请输入账户名');
					return;
				}
				if(!this.bankName){
					this.$api.msg('请选择开户银行');
					return;
				}
				if(!this.bankAccountNo){
					this.$api.msg('请输入银行卡号');
					return;
				}
			}
			//如果是支付宝提现, 检查各项输入是否正确
			if(this.tabCurrentIndex==1){
				if(!this.alipayId){
					this.$api.msg('请输入支付宝账号');
					return;
				}
			}
			let options = {
				userDTO:{
					userUuid: this.userInfo.userUuid,
				},
				withdrawAmount: this.withdrawAmount,
				bankAccountName: this.bankAccountName,
				bankName: this.bankName,
				bankAccountNo: this.bankAccountNo,
				alipayId: this.alipayId,
				paymentMethod: this.tabCurrentIndex===0?'BANK':'ALIPAY'
			}
			this.$api.request.userToCash(options, res => {
				if (res.body.status.statusCode === '0') {
					this.$api.msg('提现申请已提交');
					this.userInfo.availableBalance = this.userInfo.availableBalance - this.withdrawAmount;
					this.login(this.userInfo);
					setTimeout(()=>{
						this.$api.prePage().refreshList();
						uni.navigateBack();
					}, 800)
				} else {
					console.log(res.body.status.errorDesc);
				}
			}, false);
		},
		onConfirm(val){
			this.bankName = val.checkArr.label;
		},
		showBankList(){
			this.$refs['bank'].show();	
		},
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		withdrawFullAmount(){
			this.withdrawAmount = this.userInfo.availableBalance;
		},
		navTo(url) {
			uni.navigateTo({
				url: url
			})
		},
		}
}
